<template>
    <Panel title="" :class="[$style.panel,cname]">
        <ul :class="$style.content">
            <li>
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t2842/350/3035567089/14791/5f6ff93d/577cf395N31e76288.png?width=1125&height=252" alt/>
            </li>
            <li>
                <div>
                    <img src="//img12.360buyimg.com/jrpmobile/jfs/t2971/333/1297567079/898/f2d2e00d/577dc28dNe5138337.png?width=108&height=108" alt="">
                    <p></p>
                </div>
                <div>
                    <img src="//img12.360buyimg.com/jrpmobile/jfs/t2824/256/2966087355/831/188bfa25/577cf3dcN18aadbf2.png?width=108&height=108" alt="">
                    <p></p>
                </div>
                <div>
                    <img src="//img12.360buyimg.com/jrpmobile/jfs/t2920/282/1283157010/1040/23f1430b/577cf3e5N53f740b8.png?width=108&height=108" alt="">
                    <p></p>
                </div>
            </li>
            <li>Copyright  2004-2018 JD.com </li>
            <li></li>
            <li></li>
            <li></li>
        </ul>
    </Panel>
</template>
<script>
import Panel from "../core/panel.vue";
export default {
    components:{
        Panel,
    },
    props:{
        cname:{
            type:String,
            default:"",
        },
    }
}
</script>
<style lang="scss" module>
@import "../../css/element.scss";
.panel{
    @include panel;
    >h4{
        display:none;
    }
    .content{
        @include list();
        background:#f5f5f5;
        margin-bottom:100px;
        li{
            width:100%;
            text-align: center;
            color:#999;
            font-size: 24px;
            >img{
                width:100%;
                height:143px;
            }
            &:nth-child(2){
                @include list(row);
                box-sizing: border-box;
                height:186px;
                
                >div{
                    margin-top:20px;
                    width:33.333%;
                    text-align: center;
                    img{
                        width:90px;
                        height:90px;
                    }
                    p{
                        font-size:26px;
                        color:#999;
                        margin-top:12px;
                        text-align: center;
                        display:block;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow:hidden;
                    }
                }
            }
            &:nth-child(n+3){
                border-top:1px solid #ddd;
                padding:24px 0;
                height:38px;
                line-height:38px;
            }
        }
    }
}

</style>



