<template>
    <Panel title="" :class="$style.panel">
        <section :class="$style.content">
            <div :class="$style.item">
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t3670/58/543125556/2606/a65096b9/580d80afN63b1085a.png?width=76&height=76" alt="">
                <div>
                    <h4></h4>
                    <p>4.50%</p>
                </div>
                <em> ></em>
            </div>
            <div :class="$style.item">
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t3679/44/511862277/2728/c1c6b2bf/580d8189N6b6f8052.png?width=76&height=76" alt="">
                <div>
                    <h4></h4>
                    <p>5.50%</p>
                </div>
                <em> ></em>
            </div>
            <div :class="$style.item">
                <img src="//img12.360buyimg.com/jrpmobile/jfs/t3331/69/580199498/3074/d5bb21c5/580d7f6cN7f35e596.png?width=76&height=76" alt="">
                <div>
                    <h4></h4>
                    <p>5.80%</p>
                </div>
                <em> ></em>
            </div>
        </section>
    </Panel>
</template>
<script>
import Panel from "../core/panel.vue";
export default {
    components:{
        Panel,
    },
}
</script>
<style lang="scss" module>
    @import "../../css/element.scss";
    .panel{
        @include panel;
        >h4{
            border-bottom: 1px solid #ddd;
        }
        .content{
            @include list();
            .item{
                @include list(row);
                align-items: center;
                height:140px;
                &:after{
                    content:"";
                    height:0px;
                    width:100%;
                    box-sizing: border-box;
                    border-bottom:1px solid #ddd;
                    margin-left:136px;
                }
                &:last-child::after{
                    border-color: #fff;
                }
                >img{
                    width:88px;
                    height:88px;
                    display:inline-block;
                    margin:0 32px;
                }
                >div{
                    font-size:32px;
                    color:#333;
                    line-height:1.8;
                    width:344px;
                    p{
                        color:#ff3232;
                        font-size:24px;
                        display:block;
                        line-height: 1.1;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                }
                >em{
                    color:#ff801a;
                    font-size: 28px;
                }
            }
        }
    }
</style>



