<template lang="html">
    <div :class="$style.header">
        <span :class="$style.left">
            <em></em>&nbsp;|&nbsp;<em></em>
            <btn :class="$style.btnDownload">APP</btn>
        </span>
    </div>
</template>
<script>
import btn from "../core/btn.vue";
export default {
    components:{
        btn,
    },
}
</script>

<style lang="scss" module>
    .header{
        color:#666;
        font-size:32px;
        height:100px;
        line-height: 100px;
        position: fixed;
        top:0;
        left:0;
        right:0;
        background:#fff url(//m.jr.jd.com/spe/qyy/main/images/jr-logo.png) center center no-repeat;
        background-size: auto 50%;
        z-index:100;
        .left{
            font-size: 28px;
            height:30px;
            line-height: 30px;
            margin:17px 0 0 18px;
        }
        .btnDownload{
            float: right;
            font-size: 24px;
            height:56px;
            line-height: 56px;
            min-width: 120px;
            padding:0px;
            margin:28px 24px 0 0 ;
            border-radius: 4px;
        }
    }
</style>



